@mixin media-big-tablet {
    @media (max-width: $tablet-big-width) {
        @content;
    }
}

@mixin media-tablet {
    @media (max-width: $tablet-width) {
        @content;
    }
}

@mixin media-mobile {
    @media (max-width: $mobile-big-width) {
        @content;
    }
}

@mixin media-mobile-small {
    @media (max-width: $mobile-small-width) {
        @content;
    }
}
